@import "https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap";
html, body {
  height: 100%;
  width: 100%;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  overflow-x: hidden;
}

div#main {
  width: 100vw;
  flex-direction: column;
  display: flex;
}

section {
  width: 100vw;
}

section.min-height {
  min-height: 40vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  height: 100%;
  object-fit: cover;
  pointer-events: none;
  width: 100vw;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

.home-container {
  text-align: center;
  flex-wrap: wrap-reverse;
  justify-content: center;
  align-items: center;
  margin-top: 6rem;
  display: flex;
}

h1 {
  opacity: 0;
  margin: 0;
  font-family: Cormorant Garamond, serif;
  font-size: 7rem;
  font-weight: 400;
  line-height: .8;
  animation: slideFromLeft 1s forwards;
}

@keyframes slideFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }

  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}

h2 {
  margin: 0;
  padding-top: 1rem;
  font-size: 2rem;
  font-weight: 400;
}

.home-container h2 {
  opacity: 0;
  animation: delayAppear 1.5s ease-in-out forwards;
}

@keyframes delayAppear {
  75% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@media screen and (max-width: 570px) {
  h1 {
    font-size: 4rem;
  }

  h2 {
    font-size: 1.5rem;
  }
}

h3 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 400;
}

p {
  margin-bottom: 0;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5;
}

p.large {
  font-size: 20px;
}

p.skill-heading {
  font-family: Montserrat, sans-serif;
  font-size: 30px;
  font-weight: 500;
}

.skillset-box {
  text-align: center;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  margin: 2rem 3rem;
  font-size: 1.25rem;
  display: grid;
}

@media screen and (max-width: 900px) {
  .skillset-box {
    grid-template-columns: repeat(1, 1fr);
    margin: 2rem -1rem;
  }
}

.skill-name-box {
  align-items: center;
  gap: 1rem;
  margin-left: 5rem;
  display: flex;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #793287c0;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  color: #793287c0;
}

.container {
  grid-gap: 2rem;
  max-width: 80%;
  grid-template-columns: repeat(2, 1fr);
  margin: 2rem auto;
  display: grid;
}

@media screen and (max-width: 900px) {
  .container {
    grid-template-columns: repeat(1, .7fr);
    justify-content: center;
  }
}

.portfolio-content {
  padding-top: "3rem";
  display: flex;
}

@media screen and (max-width: 1200px) {
  .portfolio-content {
    flex-direction: column;
  }
}

.container .box {
  text-align: center;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 10px;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  display: flex;
  position: relative;
}

.container .box:after, .container .box:before {
  content: "";
  height: 100%;
  width: 100%;
  z-index: -1;
  translate: -50%, -50%;
  background-image: conic-gradient(#0000, #01068b, #74058a, #ac097e, #0000);
  border-radius: 10px;
  padding: 3px;
  position: absolute;
  bottom: -3px;
  right: -3px;
}

img.socialIcon {
  height: 30px;
  width: 30px;
}

